import {
	Vector3,
	Euler,
} from "three"
import initThree from "./composables/initThree";
import useControls from "./composables/useControls";
import useLoader from "./composables/useLoader";
import raycastSprite from "./composables/raycastSprite";
import createRoom from "./composables/createRoom";
import createSprite from "./composables/createSprite";

const {
	scene,
	camera,
	renderer
} = initThree();

useControls(camera);

useLoader();

function initRoom() {
	const living_room = createRoom({
		scene,
		name: "",
		texture_url: "./images/livingroom",
		position: new Vector3(0, 0, 0),
		euler: new Euler(0, 0, 0)
	})

	const kitchen = createRoom({
		scene,
		name: "",
		texture_url: "./images/kitchen",
		position: new Vector3(-5, 0, -10),
		euler: new Euler(0, -Math.PI / 2, 0)
	})

	const balcony = createRoom({
		scene,
		name: "",
		texture_url: "./images/balcony",
		position: new Vector3(0, 0, 15),
		euler: new Euler(0, 0, 0)
	});

	const hallway = createRoom({
		scene,
		name: "",
		texture_url: "./images/corridor",
		position: new Vector3(-15, 0, 0),
		euler: new Euler(0, -Math.PI + Math.PI / 16, 0)
	});

	const main = createRoom({
		scene,
		name: "",
		texture_url: "./images/bedroom",
		position: new Vector3(-26, 0, 2),
		euler: new Euler(0, 0, 0)
	});

	scene.add(living_room);
	scene.add(kitchen);
	scene.add(balcony);
	scene.add(hallway);
	scene.add(main);
}

function initSprite() {
	// 
	const text_living_to_balcony = createSprite({text: "", position: new Vector3(0, 0, 4)})
	// 
	const text_balcony_to_living = createSprite({text: "", position: new Vector3(-1, 0, 11)});

	// 
	const text_living_to_kit = createSprite({text: "", position: new Vector3(-1, 0, -3)});
	// 
	const text_kit_to_living = createSprite({text: "", position: new Vector3(-4, 0, -6)});

	// 
	const text_living_to_hallway = createSprite({text: "", position: new Vector3(-4, 0, 0.5)});
	// 
	const text_hallway_to_living = createSprite({text: "", position: new Vector3(-11, 0, 0)});

	// 
	const text_hallway_to_main = createSprite({text: "", position: new Vector3(-19, 0, 0)});
	// 
	const text_main_to_hallway = createSprite({text: "", position: new Vector3(-23, 0, -2)});

	scene.add(text_living_to_balcony);
	scene.add(text_balcony_to_living);

	scene.add(text_living_to_kit);
	scene.add(text_kit_to_living);

	scene.add(text_living_to_hallway);
	scene.add(text_hallway_to_living);

	scene.add(text_hallway_to_main);
	scene.add(text_main_to_hallway);

	return [
		text_living_to_balcony,
		text_balcony_to_living,
		text_living_to_kit,
		text_kit_to_living,
		text_living_to_hallway,
		text_hallway_to_living,
		text_hallway_to_main,
		text_main_to_hallway
	]
}

initRoom();

const sprites = initSprite();

raycastSprite(camera, sprites);

function render() {
	renderer.render(scene, camera);
	requestAnimationFrame(render);
}

render();
